import React, { useState, useEffect } from 'react';
import connect from '@vkontakte/vk-connect';
import View from '@vkontakte/vkui/dist/components/View/View';
import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';
import Linq from 'linq/linq';
import '@vkontakte/vkui/dist/vkui.css';

import Home from './panels/Home';
import GoodRequest from './panels/GoodRequest';
import Requests from './panels/Requests';
import Payments from './panels/Payments';
import Accounting from './panels/Accounting';

const App = () => {
	const [activePanel, setActivePanel] = useState('home');
	const [fetchedUser, setUser] = useState(null);
	const [token, setToken] = useState(null);
	const fetchedGroup = "Shop Schrodinger. Прием заказов.";
	const groupId = 139136005;
	const [isEditor, setIsEditor] = useState(null);
	const [popout, setPopout] = useState(<ScreenSpinner size='large' />);

	useEffect(() => {
		connect.subscribe(({ detail: { type, data }}) => {
			if (type === 'VKWebAppUpdateConfig') {
				const schemeAttribute = document.createAttribute('scheme');
				schemeAttribute.value = data.scheme ? data.scheme : 'client_light';
				document.body.attributes.setNamedItem(schemeAttribute);
			}
		});
		async function fetchData() {
			const user = await connect.sendPromise('VKWebAppGetUserInfo');
			const token = await connect.sendPromise("VKWebAppGetAuthToken", {"app_id": 7198667, "scope": "groups"});
			const responseEditors = await connect.sendPromise("VKWebAppCallAPIMethod", 
			{"method": "groups.get",
			"request_id": "GroupsButtons",
			"params": {"param_extended": "1",
			 			"param_user_id": 42708608/*props.fetchedUser.id*/,
			 			"param_filter": "admin, editor, moder",
			 			"v":"5.103",
						 "access_token":token
					}
			});
			const isEditor = responseEditors.Where(i => i.id == groupId).from(Array).lenght>0

			setUser(user);
			setToken(token);
			setIsEditor(isEditor);
			setPopout(null);
		}
		fetchData();
	}, []);

	const go = e => {
		setActivePanel(e.currentTarget.dataset.to);
	};

	return (
		<View activePanel={activePanel} popout={popout}>
			<Home id='home' fetchedUser={fetchedUser} go={go} fetchedGroup={fetchedGroup} token={token} isEditor={isEditor}/>
			<GoodRequest id='GoodRequest' go={go} fetchedUser={fetchedUser} fetchedGroup={fetchedGroup}/>
			<Requests id='Requests' go={go} fetchedUser={fetchedUser} fetchedGroup={fetchedGroup}/>
			<Payments id='Payments' go={go} fetchedUser={fetchedUser} fetchedGroup={fetchedGroup}/>
			<Accounting id='Accounting' go={go} fetchedUser={fetchedUser} fetchedGroup={fetchedGroup}/>
		</View>
	);
}

export default App;

